
import axios from './../fetch.js';
import Group from 'vux/src/components/group/index.vue'
import Blur from 'vux/src/components/blur/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'
import Card from 'vux/src/components/card/index.vue'
import Flexbox from 'vux/src/components/flexbox/flexbox.vue'
import FlexboxItem from 'vux/src/components/flexbox/flexbox-item.vue'
import Selector from 'vux/src/components/selector/index.vue'
import XInput from 'vux/src/components/x-input/index.vue'


export default {
components: {Group, Blur, XButton, Card, Flexbox, FlexboxItem, Selector, XInput},
data()
{
return {
point     : 100,
value     : 100,
isrotating: false,
list      : [],
};
},
mounted()
{
axios.get('./../../static/data/Goods.json',
/*    axios.get('/wap/news/newsList.Get',{
params:{
type:0,
page:0
}*/
)
.then((response) => {
this.list = response.data.ret.data.news;
console.log(this.list);
})
.catch((error) => {
console.log(error);
});
},
};



